.autocomplete-input {
    font-family: 'Trebuchet MS', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 20px;
    color: white;
    text-align: center;
    line-height: 1.8;

    outline: none;
    background-color: transparent;
    border: none;
    border-bottom: solid white 1px;
}

.autocomplete-input::placeholder {
    color: white;
}

.autocomplete-dropdown {
    margin: auto;
    width: min-content;
    text-align: center;
    position: relative
}

.dropdown-content {
    position: absolute;
    background-color: white;
    color: black;
    width: 100%;

    font-size: 18px;
    line-height: 1.8;
    text-align: left;

    border-radius: 0 0 10px 10px 
}

.dropdown-item {
    padding: 5%;
}