:root {
    --main-color: rgba(50, 50, 50, 0.5);
    --transition-time: 1000;
    font-family: 'Trebuchet MS', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: white
}

#main {
    z-index: 1;
    position: relative;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#form {
    height: min-content;
    margin: auto;
    background-color: var(--main-color);
    border-radius: 20px;

    align-items: center;
}

#form-fields {
    grid-template-columns: 1fr 125px;
    justify-content: center;

    column-gap: 20px;
    height: 40px;
}

#url {
    padding-left: 20px;
}

#button {
    width: 125px;
    height: 100%;
    outline: none;
    color: white;
    background-color: rgba(0, 0, 0, 0);
    border: solid white;
}

#button:active {
    color: rgba(150, 150, 150, 1);
    border: solid rgba(150, 150, 150, 1)
}

#loader {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 0;
}

#loader-circle {
    border: 4px solid white;
    border-top: 4px solid rgba(50, 50, 50, 1);
    border-radius: 50%;
    width: 30px;
    height: 30px;
    animation: load 1.5s linear infinite;

    margin-bottom: 1.5vh;
}

#playlist {
    min-width: 175px;
    width: 50vw;
    max-width: 500px;
    border-radius: 20px;

    margin-top: 10px;
    margin-bottom: auto;

    height: 450px !important;
}

.field {
    border-radius: 100px;
    outline: none;
    border: none;
    font-family: 'Trebuchet MS', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 16px;
}

.toggle-bar {
    background-color: rgba(0, 0, 0, 0);
    border: solid white;

    height: 20px;
    width: 40px;
    border-radius: 50px;

    margin: 0.5vh auto -1vh;
}

.toggle-point {
    height: 20px;
    width: 20px;
    border-radius: 50px;
    background-color: rgba(150, 150, 150, 1);

    margin-left: auto;
}

.text-container {
    margin: 2vh auto;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.text {
    width: auto;
    height: min-content;
    margin: 0;
    text-align: center;
}

.error-message {
    text-align: center;
    color: rgb(255, 137, 137)
}

@media (max-width: 550px) {
    #form {
        width: 70vw !important;
        padding: 4vw !important
    }

    #playlist {
        width: 78vw
    }
}

@media (max-width: 450px) {
    #form {
        width: 70vw !important;
        padding: 4vw !important;
    }

    #form-fields {
        grid-template-columns: 1fr;
        grid-template-rows: 40px 40px;
        row-gap: 10px;
        justify-items: center;
        height: min-content;
    }

    #playlist {
        width: 78vw
    }
}

@media (max-height: 615px) {
    #playlist {
        height: 152px !important;
    }
}

@media (max-height: 350px) {
    #rec-queries {
        display: flex;
        align-items: center;
        margin: auto;
        width: min-content;
    }

    #form {
        min-width: min-content;
        width: auto !important;
        display: grid !important;
        grid-template-rows: auto auto;
        /* justify-content: center; */
        padding: 5px 20px !important;
    }

    #form-fields {
        margin: 0 auto !important;
        column-gap: 5px;
    }
    
    #toggle-control {
        margin-right: 10px;
    }

    .toggle-bar {
        margin: 1vh 0 0.1em;
    }
}

iframe {
    margin: 2vh
}

@keyframes load {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg)
    }
}